.btn-wk {
  position: relative;
  font-size: 1.5rem;
  transition: all 0.5s;
  font-weight: 800;
  letter-spacing: 0.3rem;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
}
.btn-wk:active {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.btn-wk:hover {
  transform: translateY(-4px);
  -webkit-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  -moz-box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
  box-shadow: 0px 47px 46px 10px rgba(0, 0, 0, 0.71);
}
.btn-wk:hover {
  text-decoration: none;
}
.btn-wk::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all 0.4s;
  background-color: inherit;
}
.btn-wk:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}
.btn-wk__alt {
  border: none;
  color: #fff;
  text-align: center;
  font-size: 32px;
  padding: 16px;
  width: 220px;
  transition: all 0.5s;
  cursor: pointer;
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  width: 100%;
}
.btn-wk__alt:after {
  content: "";
  position: absolute;
  opacity: 0;
  top: 14px;
  right: -20px;
  transition: 0.5s;
}
.btn-wk__alt:hover {
  padding-right: 24px;
  padding-left: 8px;
}
.btn-wk__alt:hover:after {
  opacity: 1;
  right: 10px;
}

.btn-wraper {
  display: flex;
}
.btn-wraper.search {
  width: 100%;
}

