.card__side {
  height: 43rem;
  transition: all 0.8s ease;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 3px;
  overflow: hidden;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
  border-radius: 2rem;
}
.card__side--front {
  background-color: white;
}
.card__side--front-2 {
  background-image: url("../images/o-que-fazemos.jpg");
  /* linear-gradient(
  to right bottom,
  var(--color-primary-light),
  var(--color-primary-dark) */
}
.card__side--back {
  transform: rotateY(180deg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-width: 100%;
}
.card__side--back-1 {
  background-image: url("../images/sobre-nos.jpg");
  /* linear-gradient(
    to right bottom,
    var(--color-secondary-light),
    var(--color-secondary-dark)
  ); */
}
.card__side--back-3 {
  background-image: url("../images/orcamento.jpg");
  /* linear-gradient(
    to right bottom,
    var(--color-tertiary-light),
    var(--color-tertiary-dark)
  ); */
}
.card:hover .card__side--front {
  transform: rotateY(-180deg);
}
.card:hover .card__side--back {
  transform: rotateY(0);
}
.card__picture {
  background-size: cover;
  height: 23rem;
  background-blend-mode: screen;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}
.card__picture--1 {
  background-image: linear-gradient(to right bottom, var(--color-secondary-light), var(--color-secondary-dark)), url(../images/nat-5.jpg);
}
.card__picture--2 {
  background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark)), url(../images/nat-6.jpg);
}
.card__picture--3 {
  background-image: linear-gradient(to right bottom, var(--color-tertiary-light), var(--color-tertiary-dark)), url(../images/nat-7.jpg);
}
.card__heading {
